*,
*:before,
*:after {
  box-sizing: border-box;
}

*:focus {
  outline: 0 !important;
}

hr {
  margin: 10px 0;
}
nav {
  margin:0 !important;
}


.error,
.error:focus {
  box-shadow: 0 0 2px red  !important;
  color: red;
}

.full {
  height: 100%;
  width : 100%;
}
.flex-rows {
  display: flex;
  flex-direction: column;
}
/*.flex-cols {
    display: flex;
    flex-direction: row;
}*/
.flex-min {
  flex-shrink: 0;
}
.flex-max {
  flex: auto; // 1 1 auto
}
.place {
  position: relative;
  overflow: hidden;
}
.frame  {
  position: absolute;
  overflow: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
